  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 17px;
  font-weight: bold;
  height: 52px;
  line-height: 20px;
  padding: 14px 0;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  width: 259px;
